.menu {
  top:0px;
  display: flex;
  width: 100%;
  height: 10vh;
  background-color: white;
  align-items: center;
  justify-content: space-between;
  border-bottom: 2px solid #00bedd;
  position: sticky; 
  position: -webkit-sticky; 
}
.menu .menuLogo #menuTitle {
  width: 70%;
  font-size: 1.8rem;
  color: #005489;
  font-weight: normal;
  text-align: left;
  width: 100%;
}

.menu .menuLogo {
  margin-left: 5vw;
  height: 100%;
  width: 20rem;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.menu .menuLogo #logo{
  height: 100%;
  width: 30%;
}

.menu .menuOptions{
  display: flex;
  margin-right: 2vw;
  font-size: 1.25em;
  justify-content: right;
  width: 100%;
  
}
.menu .menuOptions .menuButton {
  background-color: white;
  text-decoration: none;
  color: #005489;
  font-weight: bold; 
  padding: 10px;
  margin-left:0.5rem ;
  cursor: pointer;  
  transition: color 200ms;
  border: 0;
  font-size: 20px;
  text-align: center;
}
.menu .menuButton:hover {
  font-size: 22px;  
}




